rmal normal 14px/1 FontAwesome;\r\n    font-size: inherit;\r\n    text-rendering: auto;\r\n    font-size: 13px;\r\n    opacity: 0;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    color: #fff;\r\n}\r\n\r\n\r\n.remember-me input {\r\n    display: none;\r\n}\r\n\r\n\r\n.remember-me input:checked ~ .custom-checkbox {\r\n    background-color: #4b74ff;\r\n}\r\n\r\n\r\n.remember-me input:checked ~ .custom-checkbox:before {\r\n    opacity: 1;\r\n}\r\n\r\n\r\n.forgot-password {\r\n    font-size: 16px;\r\n    color: #4a90e2;\r\n    text-decoration: underline;\r\n    cursor: pointer;\r\n}\r\n\r\n\r\n.pull-left {\r\n    text-align: left\r\n}\r\n\r\n\r\n.pull-right{\r\n    text-align: right\r\n}\r\n\r\n\r\n.card-footer{\r\n    background-color: transparent\r\n}"